import { bootstrap } from '@angular/platform-browser-dynamic';
import { enableProdMode } from '@angular/core';
import { SmartadminAppComponent, environment, navigation, I18nService} from './app/';
import {provideStore} from "@ngrx/store";
import {HTTP_PROVIDERS} from "@angular/http";
import 'jquery'
import 'jquery-ui'
import 'smart-widgets'
import 'smart-notification'
import 'smart-chat'



if (environment.production) {
  enableProdMode();
}

bootstrap(SmartadminAppComponent,[
  HTTP_PROVIDERS,
  provideStore({navigation}),
  I18nService
]);

